html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.swal2-popup {
  font-size: 2rem !important;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #A9A9A9;
  margin-bottom: 5rem;
}

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #31b539;
  padding: 2rem;
}
.dashboard__header-grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .dashboard__header-grid {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.dashboard__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (min-width: 1024px) {
  .dashboard__logo {
    text-align: left;
    margin: 0;
  }
}
.dashboard__form {
  display: flex;
  justify-content: center;
}
.dashboard__submit--logout {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
}
.dashboard__submit--logout:hover {
  color: #F8FAFC;
  cursor: pointer;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto;
}
.dashboard__sidebar {
  background-color: #2a9d32;
  width: 9.5rem;
}
@media (min-width: 768px) {
  .dashboard__sidebar {
    width: 15rem;
  }
}
@media (min-width: 480px) {
  .dashboard__sidebar {
    width: 9.5rem;
  }
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
@media (min-width: 768px) {
  .dashboard__menu {
    padding: 2rem;
  }
}
@media (min-width: 480px) {
  .dashboard__menu {
    padding: 1rem;
  }
}
.dashboard__enlace {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
  transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;
}
.dashboard__enlace:hover, .dashboard__enlace--actual {
  background-color: #3ecb47;
  color: #FFFFFF;
  border-radius: 1rem;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu-texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0.5rem;
}
.dashboard__contenido {
  width: 100%;
  padding: 1rem 1rem 3rem 1rem;
  overflow-y: auto;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
@media (min-width: 480px) {
  .dashboard__contenido {
    padding: 1rem;
  }
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 5rem;
  }
}
.dashboard__boton {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #f5f5f5;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.4rem;
}
.dashboard__boton:hover {
  background-color: #009591;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #f5f5f5;
  padding: 2rem;
  border-radius: 1rem;
  margin-top: 5rem;
  overflow-x: auto;
}
.dashboard__formulario {
  width: min(95%, 80rem);
  margin: 0 auto;
}
.dashboard__grafica {
  width: min(95%, 60rem);
  margin: 0 auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
  font-family: "Outfit", sans-serif;
  margin-bottom: 2rem;
  overflow-x: auto;
}
.table__thead {
  background-color: #5ec85e;
  color: #f5f5f5;
}
.table__th, .table__td {
  padding: 1rem;
  text-align: left;
  font-size: 1.6rem;
  vertical-align: middle;
}
@media (min-width: 480px) {
  .table__th {
    font-size: 1.4rem;
  }
}
@media (min-width: 768px) {
  .table__th {
    font-size: 1.8rem;
  }
}
@media (min-width: 1024px) {
  .table__th {
    font-size: 2rem;
  }
}
@media (min-width: 1200px) {
  .table__th {
    font-size: 1.8rem;
  }
}
.table__tr {
  height: 4rem;
}
.table__tr:nth-of-type(even) {
  background-color: #F8FAFC;
}
.table__tr:nth-of-type(odd) {
  background-color: #f5f5f5;
}
.table__td {
  padding: 1.5rem 1rem;
  font-size: 1.4rem;
  vertical-align: middle;
  text-align: left;
}
@media (min-width: 480px) {
  .table__td {
    font-size: 1.2rem;
    padding: 1rem 0.8rem;
  }
}
@media (min-width: 768px) {
  .table__td {
    font-size: 1.6rem;
    padding: 1.2rem 1rem;
  }
}
@media (min-width: 1024px) {
  .table__td {
    font-size: 1.8rem;
    padding: 1.5rem 1.2rem;
  }
}
@media (min-width: 1200px) {
  .table__td {
    font-size: 1.5rem;
    padding: 2rem 1.5rem;
  }
}
.table__td--acciones {
  text-align: right;
}
.table__td--descripcion {
  max-width: 23rem;
  position: relative;
}
.table__td--descripcion .table__descripcion {
  max-height: 18rem;
  overflow-y: auto;
  white-space: normal;
  padding-right: 0.5rem;
  background-color: transparent;
  padding: 0;
  font-family: "Outfit", sans-serif;
  font-size: 1rem;
  color: #4A4A4A;
  text-align: left;
  line-height: 1.6;
  margin-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
@media (min-width: 480px) {
  .table__td--descripcion .table__descripcion {
    font-size: 1.2rem;
  }
}
@media (min-width: 768px) {
  .table__td--descripcion .table__descripcion {
    font-size: 1.6rem;
  }
}
@media (min-width: 1024px) {
  .table__td--descripcion .table__descripcion {
    font-size: 1.8rem;
  }
}
@media (min-width: 1200px) {
  .table__td--descripcion .table__descripcion {
    font-size: 2rem;
  }
}
.table__td--descripcion .table__descripcion::-webkit-scrollbar {
  width: 0.5rem;
}
.table__td--descripcion .table__descripcion::-webkit-scrollbar-thumb {
  background-color: transparent;
  border-radius: 0.2rem;
}
.table__td--descripcion .table__descripcion::-webkit-scrollbar-thumb:hover {
  background-color: #d8dde3;
}
.table__td--descripcion .table__descripcion::-webkit-scrollbar-track {
  background-color: transparent;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 700;
  display: inline-flex;
  align-items: center;
  margin-left: 0.5rem;
}
.table__accion--editar {
  color: #007df4;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}
.table__accion:hover {
  cursor: pointer;
}
@media (min-width: 480px) {
  .table__accion {
    font-size: 1rem;
  }
}
@media (min-width: 768px) {
  .table__accion {
    font-size: 1.4rem;
  }
}
@media (min-width: 1024px) {
  .table__accion {
    font-size: 1.6rem;
  }
}
@media (min-width: 1200px) {
  .table__accion {
    font-size: 1.7rem;
  }
}
.table .imagen-tabla {
  width: 10rem; /* Ajusta el ancho según sea necesario */
  height: auto; /* Mantiene la proporción de la imagen */
}
.table .encargado {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.table .encargado__imagen {
  width: 10rem;
  height: auto;
  margin-bottom: 1rem;
}
.table .encargado__info {
  text-align: center;
}
.table .encargado__nombre {
  font-size: 1.4rem;
  font-weight: 700;
}
.table .encargado__puesto {
  font-size: 1.2rem;
  color: #A9A9A9;
}

@media (max-width: 480px) {
  .table {
    display: block;
    width: 100%;
  }
  .table__thead {
    display: none;
  }
  .table__tr {
    display: block;
    margin-bottom: 1rem;
    border: none;
  }
  .table__td {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem;
    text-align: left;
    border-bottom: 1px solid #d8dde3;
  }
  .table__td:last-child {
    border-bottom: none;
  }
  .table__td:before {
    content: attr(data-label);
    font-weight: 700;
    margin-bottom: 0.5rem;
  }
  .table__td--acciones {
    flex-direction: row;
    justify-content: flex-end;
    width: 100%;
  }
  .encargado__imagen {
    width: 8rem;
  }
  .encargado__nombre, .encargado__puesto {
    font-size: 1.2rem;
  }
}
.paginacion {
  margin-top: 5rem;
  margin-bottom: 3rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #00c8c2;
  font-size: 1.6rem;
}
.paginacion__enlace--texto {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid #00c8c2;
}
.paginacion__enlace--texto:hover {
  background-color: #007c78;
  color: #FFFFFF;
}
.paginacion__enlace--numero {
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--actual {
  color: #64748B;
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}

.listado-ponentes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
.listado-ponentes__ponente, .listado-ponentes__ponente--seleccionado {
  color: #007df4;
  border: 1px solid #007df4;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
}
.listado-ponentes__ponente:hover, .listado-ponentes__ponente--seleccionado:hover {
  cursor: pointer;
}
.listado-ponentes__ponente--seleccionado {
  background-color: #007df4;
  color: #FFFFFF;
}

.bloques {
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bloques {
    margin-top: 10rem;
  }
}
.bloques__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1024px) {
  .bloques__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.bloque {
  padding: 3rem;
  color: #FFFFFF;
  border-radius: 1rem;
}
.bloque:nth-child(1) {
  background-image: linear-gradient(to left top, #f97316, #fba061);
}
.bloque:nth-child(2) {
  background-image: linear-gradient(to left top, #A855F7, #cd9efa);
}
.bloque:nth-child(3) {
  background-image: linear-gradient(to left top, #588b0f, #84d016);
}
.bloque:nth-child(4) {
  background-image: linear-gradient(to left top, #17769E, #23a7df);
}
.bloque__heading {
  margin: 1rem 0 2rem 0;
  font-size: 3.6rem;
}
.bloque__texto {
  margin: 1rem 0;
}
.bloque__texto--cantidad {
  margin-top: 5rem;
  font-weight: 900;
  font-size: 6rem;
  text-align: center;
}

.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
}
.auth__texto {
  font-size: 2rem;
}

.teleurban__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .teleurban__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.teleurban__texto {
  line-height: 2;
}

.resumen {
  background-image: linear-gradient(to right, rgba(0, 125, 244, 0.7), rgba(0, 125, 244, 0.9)), url(../img/bg_numeros.jpg);
  background-image: linear-gradient(to right, rgba(0, 125, 244, 0.7), rgba(0, 125, 244, 0.9)), image-set(url(../img/bg_numeros.avif) 1x, url(../img/bg_numeros.webp) 1x, url(../img/bg_numeros.jpg) 1x);
  padding: 12rem 0;
  background-size: cover;
  background-position: center;
}
.resumen__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .resumen__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.resumen__bloque {
  text-align: center;
  color: #FFFFFF;
}
.resumen__texto, .resumen__texto--numero {
  font-weight: 900;
  line-height: 1;
  margin: 0;
  font-size: 2.4rem;
  text-transform: uppercase;
}
.resumen__texto--numero {
  font-size: 8rem;
}

.speakers__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .speakers__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .speakers__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.speaker {
  border: 1px solid #7f8ea3;
  padding: 3rem;
  background-position: center top;
  background-size: 350px;
  background-repeat: no-repeat;
}
.speaker__nombre {
